// Dependencies
import React, { useEffect, useState } from "react";
import Setup from "./components/setup";
import TopMenu from "./components/topmenu";
import axios from "axios";
import StoreContext from "./context/store";
import useIDB from "./hooks/useIDB";

// Styles
import "./tailwind.output.css";

const App = () => {
  const [setup, setSetup] = useState({ server: "", agent: "" });
  const [delivery, setDelivery] = useState("");
  const [db, setDb] = useState();

  //const { db } = useIDB();

  useEffect(() => {
    const request = indexedDB.open("store", 1);
    request.onsuccess = (event) => {
      setDb(event.target.result);
    };
    request.onupgradeneeded = (event) => {
      if (!request.result.objectStoreNames.contains("setup")) {
        request.result.createObjectStore("setup", { keyPath: "id" });
      }
      if (!request.result.objectStoreNames.contains("delivery")) {
        request.result.createObjectStore("delivery", { keyPath: "id" });
      }
    };
  }, []);

  useEffect(() => {
    if (db) {
      try {
        const tr = db.transaction("setup", "readwrite");
        const st = tr.objectStore("setup");

        st.openCursor().onsuccess = (event) => {
          var cursor = event.target.result;
          if (cursor) {
            //alert("Name for SSN " + cursor.key + " is " + cursor.value);
            if (cursor.key == "server") {
              setSetup((prev) => {
                return { ...prev, server: cursor.value.value };
              });
            }
            if (cursor.key == "agent") {
              setSetup((prev) => {
                return { ...prev, agent: cursor.value.value };
              });
            }
            cursor.continue();
          } else {
            //alert("No more entries!");
          }
        };
      } catch (error) {}
    }
  }, [db]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const result = await axios(
          `https://${setup.server}.eu.ngrok.io/dev-voda/hs/mobvoda/get_dostavka/${setup.agent}`
        );

        console.log(result);

        if (result.status == 200) {
          setDelivery({
            uidd: result.data.UID,
            delivery: result.data.Доставка.map((el) => {
              return {
                client_id: el.КлиентUID,
                client: el.Клиент,
                adres_id: el.АдресUID,
                adres: el.Адрес,
                cargo: result.data.Груз
                  .filter((elg) => elg.АдресUID === el.АдресUID)
                  .map((elg) => {
                    return {
                      id: elg.ТоварUID,
                      art: elg.ТоварАртикул,
                      name: elg.Товар,
                      qt: elg.Количество
                    };
                  })
              };
            })
          });
        } else {
          setDelivery({});
        }
      } catch (error) {
        setDelivery({});
      }
    };

    if (setup.server && setup.agent) {
      try {
        fetchData();
      } catch (error) {
        setDelivery({});
      }
    } else {
      setDelivery({});
    }
  }, [setup]);

  return (
    <StoreContext.Provider
      value={{ setup, setSetup, delivery, setDelivery, db }}
    >
      <div className="min-h-screen max-h-screen">
        <TopMenu />
        <div className="flex-1">
          <Setup />
          <ul
            className="overflow-scroll overflow-x-hidden"
            Style="height: calc(100vh - 200px) "
          >
            {delivery &&
              delivery.delivery &&
              delivery.delivery.map((el) => (
                <li>
                  <a
                    href=""
                    className="flex justify-between items-center border-b-2 space-y-1"
                  >
                    <div className="">
                      <p className="text-lg font-bold">{el.adres}</p>
                      <p className="text-xs">{el.client}</p>
                    </div>
                    <div className="p-2 text-sm">
                      {el.cargo.map((elc) => (
                        <div>
                          <span>{elc.art}</span>
                          <span>-</span>
                          <span>{elc.qt}</span>
                        </div>
                      ))}
                    </div>
                  </a>
                </li>
              ))}
          </ul>
        </div>
      </div>
    </StoreContext.Provider>
  );
};

export default App;
